@import url(https://fonts.googleapis.com/css2?family=Permanent+Marker&display=swap);
:root {
	/* FLUENT UI https://developer.microsoft.com/en-us/fluentui#/styles/web/colors/neutrals */

	/* COLORS */
	--blueGray900: #263238;
	--blueGray800: #37474f;
	--blueGray700: #455a64;
	--blueGray600: #546e7a;
	--blueGray500: #607d8b;
	--blueGray400: #78909c;
	--blueGray300: #90a4ae;
	--blueGray200: #b0bec5;
	--blueGray100: #cfd8dc;
	--blueGray50: #eceff1;

	--cyanA200: #18ffff;
	--cyanA400: #00e5ff;
	--indigoA700: #304ffe;
	--deepOrangeA400: #ff3d00;

	/* FONT SIZE */
	--fontSize1: 3.052rem;
	--fontSize2: 2.441rem;
	--fontSize3: 1.953rem;
	--fontSize4: 1.563rem;
	--fontSize5: 1.25rem;
	--fontSize6: 1rem;
	--fontSize7: 0.8rem;

	/* FONT WEIGHT */
	--black: 900;
	--bold: 700;
	--medium: 500;
	--regular: 400;
	--light: 300;
}

* {
	box-sizing: border-box;
	margin: 0;
	padding: 0;
}

html {
	font-size: 24px; /* Root font size */
}

body {
	font-family: "Permanent Marker", cursive;
	color: #263238;
	color: var(--blueGray900);
}

.App {
	text-align: center;
	margin: 1rem;
}

a {
	text-decoration: none;
}

/* TYPOGRAPHY */

h1 {
	font-size: 3.052rem;
	font-size: var(--fontSize1);
	font-weight: 900;
	font-weight: var(--black);
}

h2 {
	font-size: 2.441rem;
	font-size: var(--fontSize2);
	font-weight: 900;
	font-weight: var(--black);
}

h3 {
	font-size: 1.953rem;
	font-size: var(--fontSize3);
	font-weight: 900;
	font-weight: var(--black);
}

.text {
	font-size: 1.25rem;
	font-size: var(--fontSize5);
	line-height: 150%;
}

/* --- */
.blueGray200 {
	color: #b0bec5;
	color: var(--blueGray200);
}

/* ABOUT */

.About_intro__3MsRT {
	margin-top: 8px;
	font-size: var(--fontSize5);
	color: var(--blueGray600);
}

/* NAV */

.Nav_nav__3Bj6t {
	display: flex;
	align-items: center;
	justify-content: space-between;
	width: 100%;
	padding: 16px 0;
}

.Nav_nav__logo__title__1O_uc {
	font-size: var(--fontSize4);
	color: var(--blueGray900);
}

.Nav_nav__list__2uJt8 {
	list-style: none;
	display: flex;
	/* gap: 8px; */
}

.Nav_nav__list__item__2J0Yq {
	width: 48px;
	height: 48px;
	/* background-color: var(--blueGray50); */
	border-radius: 50%;
}

.Nav_nav__list__item__link__3frUE {
	display: grid;
	place-items: center;
	width: 100%;
	height: 100%;
	font-size: 28px;
	/* font-size: var(--fontSize4); */
	color: var(--blueGray500);
}

.Nav_nav__list__item__link__3frUE:hover {
	color: var(--blueGray900);
	box-shadow: 0 2 0 var(--blueGray900);
}

/* SKILLS */

.Skills_skills__container__1gdUo {
	display: flex;
	flex-wrap: wrap;
	grid-gap: 8px;
	gap: 8px;
	margin-top: 12px;
}

.Skills_skills__container__tag__es24D {
	display: inline-block;
	padding: 8px 12px;
	background-color: var(--blueGray50);
	border-radius: 4px;
}

/* PROJECTS */

.Projects_projects__grid__2g9UU {
	display: grid;
	grid-template-columns: repeat(auto-fit, minmax(368px, 1fr));
	grid-gap: 24px;
	width: 100%;
	margin-top: 12px;
}

.Projects_projects__grid__item__1XVuK {
	padding: 20px;
	border-radius: 8px;
	box-shadow: 0 0 0 1px var(--blueGray100);
}

.Projects_projects__grid__item__caption__2isvv {
	display: flex;
	justify-content: space-between;
	align-items: center;
}

.Projects_projects__grid__item__caption__title__4eotZ {
	font-size: var(--fontSize4);
}

.Projects_projects__grid__item__caption__category__3FhN5 {
	display: inline-block;
	color: white;
	padding: 4px 6px;
	border-radius: 4px;
	font-size: var(--fontSize7);
}

.Projects_projects__grid__item__description__37Dr4 {
	margin: 12px 0;
	line-height: 150%;
	color: var(--blueGray700);
}

/* TAGS */
.Projects_projects__grid__item__tags__2MQVD {
	display: flex;
	grid-gap: 8px;
	gap: 8px;
	margin-top: 12px;
}

.Projects_projects__grid__item__tags__item__XCpE8 {
	color: var(--blueGray700);
	padding: 6px 8px;
	background-color: var(--blueGray50);
	border-radius: 4px;
}

/* LINKS */
.Projects_projects__grid__item__links__24CXA {
	display: flex;
	grid-gap: 16px;
	gap: 16px;
	margin-top: 16px;
}

.Projects_projects__grid__item__links__item__2l9U3 {
	padding: 6px 8px;
	box-shadow: 0 0 0 1px var(--blueGray100);
	border-radius: 4px;
	color: var(--blueGray700);
}

.Projects_projects__grid__item__links__item__2l9U3:hover {
	color: var(--blueGray900);
	box-shadow: 0 0 0 1px var(--blueGray900);
	text-decoration: underline;
	/* box-shadow: 0 2px 0 var(--blueGray900); */
}

/* FOOTER */

.Footer_footer__3kRR3 {
	/* height: 48px; */
	width: 100%;
	margin-top: 32px;
}

